import ReactNativeEngine from '../react-native/ReactNativeEngine';
import * as ExpoFiles from '../ExpoFiles';
const reactNativeEngine: ReactNativeEngine = new ReactNativeEngine();
class ReactNativeCodeEngine {
  constructor() {
    // this.baseCondeGen = new BaseCodeGen();
  }
  meta: any = {
    name: 'react-native',
    displayName: 'React Native',
    icon:
      'https://pre-cloud.builderx.io/assets/plugins/component/icons/React.svg',
  };
  execute: any = (fileJSON: any, config: any) => {
    return reactNativeEngine.getFileCode(fileJSON, config);
  };
  getFiles: any = () => {
    return ExpoFiles;
  };

  getFilesToWrite: any = () => {
    const filesMap = new Map();

    filesMap.set('/package.json', {
      content: JSON.stringify(ExpoFiles.packageJSON, null, 2),
    });
    filesMap.set('/app.json', {
      content: JSON.stringify(ExpoFiles.appJSON, null, 2),
    });
    filesMap.set('/babel.config.js', {
      content: ExpoFiles.babelConfigJSContent,
    });
    filesMap.set('/web/index.html', { content: ExpoFiles.indexHTML });
    filesMap.set('/.watchmanconfig', {
      content: ExpoFiles.watchmanConfigContent,
    });
    filesMap.set('/.flowconfig', { content: ExpoFiles.flowConfigContent });
    filesMap.set('/.gitignore', { content: ExpoFiles.gitIgnoreContent });
    filesMap.set('/README.md', { content: ExpoFiles.projectReadme });

    return filesMap;
  };
}

export default class ReactNativeCodeGen {
  name = 'ReactNativeCodeGen';
  session: any;
  init(session: any) {
    this.session = session;
  }
  activate() {
    const codeEngine = new ReactNativeCodeEngine();
    this.session.addCodeEngine(codeEngine);
  }

  deactivate() {}

  logSession() {
    this.session.executeFunction('logSession');
  }
}
